import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";

const Result = ({typeObj}) => {
    let history = useHistory();
    const[typeName, setTypeName] = useState("");

    useEffect(() => {
        if (typeObj.IE * typeObj.NS * typeObj.FT * typeObj.PJ == 0) {
            history.push('/')
        }

        let IE = typeObj.IE < 0 ? "I" : "E";
        let NS = typeObj.NS < 0 ? "N" : "S";
        let FT = typeObj.FT < 0 ? "F" : "T";
        let PJ = typeObj.PJ < 0 ? "P" : "J";

        setTypeName(IE + NS + FT + PJ);

    }, [typeObj]);

    return (
        <div>
            Result
            <p>IE: {typeObj.IE},
            NS: {typeObj.NS},
            FT: {typeObj.FT},
            PJ: {typeObj.PJ},
            type: {typeName}</p>
        </div>
    )
}

export default Result;